import Vue from 'vue';
import VueRouter from 'vue-router';
import Track from '../views/Track.vue';
import Charts from '../views/Charts.vue';
import Home from '../views/Home.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '*',
    redirect: 'home'
  },
  {
    path: '/home',
    name: 'Home',
    component: Home
  },
  {
    path: '/track',
    name: 'Track',
    component: Track
  },
  {
    path: '/charts',
    name: 'Charts',
    component: Charts
  }
];

const router = new VueRouter({
  routes
});

export default router;
